import React, { useState } from "react";
import { Row, Col, Form, Button, Spinner, iframe } from "react-bootstrap";
import { store } from "react-notifications-component";
import Circle from "./../../assets/images/circle.png";
import NetShape from "./../../assets/images/net-shap.png";
import { ApiHelper, getValidationErrors } from "../../../utills/helpers";
import { ContactUsValidation } from "../../validations";
import MapComponent from "./Map";
import { ToastSettings } from "../../../utills/config";
const initialState = {
  firstName: "",
  lastName: "",
  email: "",
  phone: "",
  message: "",
};
const ContactUs = () => {
  const [state, setState] = useState(initialState);
  const [errors, setErrors] = useState(initialState);
  const [isLoading, setIsLoading] = useState(false);
  /**
   *
   * @param {*} e
   */
  const onChange = (e) => {
    const { name, value } = e.target;
    const newState = Object.assign({}, state);
    newState[name] = value;
    setState(newState);
    const newErrors = Object.assign({}, errors);
    newErrors[name] = "";
    setErrors(newErrors);
  };
  /**
   *
   * @param {*} e
   */
  const onSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      ContactUsValidation.validateSync(state, {
        abortEarly: false,
      });
      const { isError, messages, data } = await new ApiHelper().FetchFromServer(
        "/contact-us",
        "",
        "POST",
        true,
        undefined,
        state
      );
      store.addNotification({
        ...ToastSettings,
        message: messages[0],
        type: "success",
      });
      setState(initialState);
    } catch (error) {
      setErrors(getValidationErrors(error));
    }

    setIsLoading(false);
  };
  return (
    <>
      <Row>
        <Col lg={9}>
          <div className="form-wrap">
            <div className="net-shape-bg">
              <img src={NetShape} width={300} alt={"Net Shape"} />
            </div>
            <div className="ribbon-heading-wrap mb-5">
              <div className="ribbon-heading">
                <h2 className="mt-2">Get In Touch</h2>
              </div>
            </div>
            <Form onSubmit={onSubmit} noValidate>
              <Row>
                <Col md={3}>
                  <div className="form-group">
                    <input
                      type="text"
                      id="fname"
                      name="firstName"
                      className="form-control"
                      onChange={onChange}
                      value={state.firstName}
                    />
                    <label className={`form-control-placeholder ${state.firstName ?  "float-label" : "" }`} htmlFor="fname">
                      First Name
                    </label>
                    {errors.firstName ? (
                      <p className="text-danger">{errors.firstName}</p>
                    ) : null}
                  </div>
                </Col>
                <Col md={3}>
                  <div className="form-group">
                    <input
                      type="text"
                      id="lname"
                      name="lastName"
                      className="form-control"
                      onChange={onChange}
                      value={state.lastName}
                    />
                    <label className={`form-control-placeholder ${state.lastName ?  "float-label" : "" }`} htmlFor="lname">
                      Last Name
                    </label>

                    {errors.lastName ? (
                      <p className="text-danger">{errors.lastName}</p>
                    ) : null}
                  </div>
                </Col>
                <Col md={3}>
                  <div className="form-group">
                    <input
                      type="email"
                      id="email"
                      name="email"
                      className="form-control"
                      onChange={onChange}
                      value={state.email}
                    />
                    <label className={`form-control-placeholder ${state.email ?  "float-label" : "" }`} htmlFor="email">
                      Email
                    </label>

                    {errors.email ? (
                      <p className="text-danger">{errors.email}</p>
                    ) : null}
                  </div>
                </Col>
                <Col md={3}>
                  <div className="form-group">
                    <input
                      type="text"
                      id="phone"
                      name="phone"
                      className="form-control"
                      onChange={onChange}
                      value={state.phone}
                    />
                    <label className={`form-control-placeholder ${state.phone ?  "float-label" : "" }`} htmlFor="phone">
                      Phone
                    </label>

                    {errors.phone ? (
                      <p className="text-danger">{errors.phone}</p>
                    ) : null}
                  </div>
                </Col>
                <Col md={12}>
                  <div className="form-group">
                    <textarea
                      type="text"
                      id="phone"
                      className="form-control"
                      name="message"
                      onChange={onChange}
                      value={state.message}
                      style={{
                        resize: "none",
                      }}
                    />
                    <label className={`form-control-placeholder ${state.message ?  "float-label" : "" }`} htmlFor="phone">
                      Message
                    </label>

                    {errors.message ? (
                      <p className="text-danger">{errors.message}</p>
                    ) : null}
                  </div>
                </Col>
              </Row>
              <Button disabled={isLoading} variant="primary" type="submit">
                {isLoading ? (
                  <>
                    <Spinner animation="border" size="sm" /> Please wait..
                  </>
                ) : (
                  "Submit"
                )}
              </Button>
            </Form>

            <div className="circle-shape-bg">
              <img src={Circle} width={300} alt={"Circle Shape"} />
            </div>
          </div>
        </Col>
        <Col lg={3}>
          <div className="map-wrap">
            <MapComponent />
            {/* <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d823778.4340080587!2d-96.37937601387219!3d36.24229940962353!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x54eab584e432360b%3A0x1c3bb99243deb742!2sUnited%20States!5e0!3m2!1sen!2sin!4v1600714492992!5m2!1sen!2sin"
              width="600"
              height="450"
              frameborder="0"
              style="border:0;"
              allowfullscreen=""
              aria-hidden="false"
              tabindex="0"
            ></iframe> */}
          </div>
        </Col>
      </Row>
    </>
  );
};

export default ContactUs;
