import React, { useEffect } from "react";
import Skeleton from "react-loading-skeleton";
import { useDispatch, useSelector } from "react-redux";
import { requestTopAuthors } from "../../../redux/actions";
import { getProfileImage, getFullName } from "../../../utills/helpers";
import UsernameComponent from "../Common/Username";
import Avatar from "react-avatar";

const TopAuthors = () => {
  const dispatch = useDispatch();
  const { isLoading, data } = useSelector((state) => state.topAuthorsReducer);
  useEffect(() => {
    dispatch(requestTopAuthors());
    // eslint-disable-next-line
  }, []);
  return (
    <>
      {/* <div className="main-heading">
        
      </div> */}
      <div className="ribbon-heading-wrap">
        <div className="ribbon-heading ligh-blue-ribbon">
          <h4 className="m-0"><i className="fas fa-pen-fancy mr-1"></i> Top Authors</h4>
        </div>
      </div>
      <div id="custom-scrollbar">
        {isLoading
          ? ["", "", "", "", "", "", "", ""].map((_, index) => (
              <div className="card-listing" key={index}>
                <div className="d-flex align-items-center">
                  <div className="user-icon-circle"></div>
                  <div className="blog-card-text ml-2">
                    <h6 className="mb-1">
                      <Skeleton width={50} />
                    </h6>
                    <Skeleton />
                  </div>
                </div>
              </div>
            ))
          : data.map((author) => (
              <div className="card-listing" key={author._id}>
                <div className="d-flex align-items-center">
                  <div className="user-icon-circle">
                    <Avatar
                      src={getProfileImage(author)}
                      name={getFullName(author)}
                      size={40}
                      round
                    />
                  </div>
                  <div className="blog-card-text ml-2">
                    <h6 className="mb-1">
                      <UsernameComponent user={author} />
                    </h6>
                    {author.about ? (
                      <p className="m-0">{author.about}</p>
                    ) : null}
                  </div>
                </div>
              </div>
            ))}
      </div>
    </>
  );
};

export default TopAuthors;
