import React, { useState, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import PostLoader from "../Common/PostLoader";
import Skeleton from "react-loading-skeleton";
import { ApiHelper } from "../../../utills/helpers";
import PostComponent from "../Common/Post";
import { Link } from "react-router-dom";
import blogThumbnailone from "../../assets/images/blog-1.png";
// import noData from "../../../assets/images/no-data.png";
const LatestPost = ({ _id: postType, name, isLoading: isLoadingFromProps }) => {
  const [state, setState] = useState({
    isLoading: true,
    data: [],
  });
  const getLatestPost = async () => {
    const { isError, data } = await new ApiHelper().FetchFromServer(
      "/homepage",
      `/latest-post/${postType}`,
      "GET",
      true
    );
    if (isError) {
      setState({
        isLoading: false,
        data: [],
      });
      return;
    }
    setState({
      isLoading: false,
      data,
    });
  };
  useEffect(() => {
    if (!isLoadingFromProps) {
      getLatestPost();
    }
    //eslint-disable-next-line
  }, [isLoadingFromProps]);
  const { isLoading, data } = state;
  return (
    <>
      {isLoading || isLoadingFromProps
        ? ["", "", "", ""].map((_, index) => <PostLoader key={index} />)
        : data.map((post) => <PostComponent key={post._id} {...post} />)}
    </>
  );
};

export default LatestPost;
