import React, { useState, useEffect } from "react";
import Skeleton from "react-loading-skeleton";
import {
  ApiHelper,
  getPostURL,
  resolvePostImageURL,
} from "../../../utills/helpers";
import { Link } from "react-router-dom";
import { PostListEngagement } from "../Common/PostListEngagement";

const TrendingPosts = ({
  _id: postType,
  name,
  isLoading: isLoadingFromProps,
}) => {
  const [state, setState] = useState({
    isLoading: true,
    data: [],
  });
  const getTrendingPosts = async () => {
    const { isError, data } = await new ApiHelper().FetchFromServer(
      "/homepage",
      `/trending-post/${postType}`,
      "GET",
      true
    );
    if (isError) {
      setState({
        isLoading: false,
        data: [],
      });
      return;
    }
    setState({
      isLoading: false,
      data,
    });
  };
  useEffect(() => {
    if (!isLoadingFromProps) {
      getTrendingPosts();
    }
    //eslint-disable-next-line
  }, [isLoadingFromProps]);
  const { isLoading, data } = state;
  return (
    <>
      <div className="main-heading">
        <div className="ribbon-heading-wrap">
          <div className="ribbon-heading ligh-blue-ribbon">
            <h4 className="m-0">
              <i className="fa fa-chart-line mr-1"></i>{" "}
              {isLoading || isLoadingFromProps ? (
                <Skeleton width={100} />
              ) : (
                name
              )}
            </h4>
          </div>
        </div>
      </div>
      {isLoading || isLoadingFromProps ? (
        ["", "", "", "", "", "", "", "", "", "", "", "", ""].map((_, index) => (
          <div className="card-listing" key={index}>
            <div className="d-flex align-items-center">
              <div className="user-icon-circle"></div>
              <div className="blog-card-text ml-2">
                <h6 className="mb-1">
                  <Skeleton height={12} />
                </h6>
                <p className="m-0">
                  <Skeleton height={10} />
                </p>
                <div className="rection-wrap">
                  <Skeleton height={8} />
                </div>
              </div>
            </div>
          </div>
        ))
      ) : data.length ? (
        data.map((post) => (
          <div key={post._id} className="card-listing">
            <div className="d-flex align-items-center">
              <div className="user-icon-circle">
                <img
                  alt=""
                  src={resolvePostImageURL(post.featuredImage)}
                  className={"img-fluid"}
                />
              </div>
              <div className="blog-card-text ml-2">
                <h6 className="mb-1">
                  <Link to={getPostURL(post)}>{post.title}</Link>
                </h6>
                <p className="m-0">{post.shortDescription}</p>
                <PostListEngagement {...post} />
              </div>
            </div>
          </div>
        ))
      ) : (
        <p>
          No Trending Post <b>{name}</b>
        </p>
      )}
    </>
  );
};

export default TrendingPosts;
