import React, { useEffect, useState } from "react";
import { Carousel } from "react-bootstrap";
import Skeleton from "react-loading-skeleton";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { requestTopPicks } from "../../../redux/actions";
import { getPostURL, resolvePostImageURL } from "../../../utills/helpers";
import { PostListEngagement } from "../Common/PostListEngagement";
import { PostTags } from "../Common/PostTags";
import { SharePost } from "../Common/SharePost";

const TopPicks = () => {
  const [seletedIndex, setSelectedIndex] = useState(0);
  const dispatch = useDispatch();
  const { isLoading, posts } = useSelector((state) => state.topPicksReducer);
  useEffect(() => {
    dispatch(requestTopPicks());
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="top-pick-section">
        <div className="top-pick-content-wrap d-md-flex">
          {isLoading ? (
            <div className="img-wrap">
              <div className="overlay-text">
                <Skeleton />
              </div>
            </div>
          ) : (
              <Carousel
                indicators={false}
                onSelect={(active) => setSelectedIndex(active)}
              >
                {posts.map((post) => (
                  <Carousel.Item key={post._id}>
                    <div className="top-pick-video-wrap">
                      <img
                        alt=""
                        src={resolvePostImageURL(post.featuredImage)}
                        className="img-fluid"
                      />
                    </div>
                  </Carousel.Item>
                ))}
              </Carousel>
            )}
          {posts[seletedIndex] ? (
            <div className="top-pick-text ml-md-4">
              <div className="ribbon-heading-wrap">
                <div className="ribbon-heading ligh-blue-ribbon">

                  <h4 className="m-0">
                    {posts[seletedIndex].categories.map((cat) => (
                      <React.Fragment key={cat._id} >
                        <Link className="blog-link-title" to={getPostURL(posts[seletedIndex])}>
                          {cat.name}
                        </Link>
                      </React.Fragment>
                    ))}
                  </h4>

                </div>
              </div>
              <Link to={getPostURL(posts[seletedIndex])}>
                <h4>{posts[seletedIndex].title}</h4>
              </Link>
              <p className="mt-4">
                {posts[seletedIndex].shortDescription.truncate(80)}
              </p>

              <PostListEngagement totalViews={posts[seletedIndex].totalViews}
                totalLikes={posts[seletedIndex].totalLikes}
                totalDislikes={posts[seletedIndex].totalDislikes}
                totalComments={posts[seletedIndex].totalComments}
                postId={posts[seletedIndex]._id} />
              <PostTags tags={posts[seletedIndex].tags} />
              <SharePost post={{ ...posts[seletedIndex], url: getPostURL(posts[seletedIndex]) }} />
            </div>
          ) : (
              <>
                <div className="top-pick-text ml-md-4">
                  <div className="ribbon-heading-wrap">
                    <div className="ribbon-heading ligh-blue-ribbon">
                      <h4 className="m-0">
                        <Skeleton width={100} />
                      </h4>
                    </div>
                  </div>
                  <h4>
                    {" "}
                    <Skeleton />
                  </h4>
                  <p className="mt-4">
                    {" "}
                    <Skeleton />
                    <Skeleton />
                    <Skeleton />
                  </p>
                  <div className="rection-wrap">
                    <Skeleton width={100} />
                  </div>
                  <div className="tag-wrap d-flex mt-3">
                    <Skeleton />
                  </div>
                  <Skeleton />
                </div>
              </>
            )}
        </div>
      </div>
    </>
  );
};

export default TopPicks;
