import React from "react";
import { Link } from "react-router-dom";
import { getProfileURL, getFullName } from "../../../utills/helpers";
import verifyIcon from "./../../assets/images/verify-icon.svg";
const UsernameComponent = ({ user }) => (
  <>
    <Link to={getProfileURL(user)}>
      {getFullName(user)}
      {user.isVerified ? (
        <>
          &nbsp;
          <img
            src={verifyIcon}
            alt="verify icon"
            style={{
              width: 12,
            }}
          />
        </>
      ) : null}
    </Link>
  </>
);

export default UsernameComponent;
