import React, { useEffect } from "react";
import { Col, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { requestPostTypes } from "../../redux/actions";
import LatestPost from "./../components/Home/LatestPost";
import TopAuthors from "./../components/Home/TopAuthors";
import TopPicks from "./../components/Home/TopPicks";
import TrendingPosts from "./../components/Home/TrendingPosts";
import ContactUs from "../components/Common/ContactUs";

const HomeContainer = () => {
  const dispatch = useDispatch();
  const { isLoading, data, isError, message: errorMessage } = useSelector(
    (state) => state.postTypeReducer
  );
  useEffect(() => {
    dispatch(requestPostTypes());
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <div className="ribbon-heading-wrap mb-4">
        <div className="ribbon-heading">
          <h2 className="mt-2">Top Picks for you</h2>
        </div>
      </div>
      <Row>
        <Col lg={9}>
          <TopPicks />
        </Col>
        <Col lg={3}>
          <div className="blog-card mt-5 mt-md-0 top-authors-wrap">
            <TopAuthors />
          </div>
        </Col>
      </Row>
      <div className="advertisement-section"></div>

      {isLoading ? (
        <Row>
          <Col lg={9}>
            <LatestPost isLoading />
          </Col>
          <Col lg={3}>
            <div className="blog-card trending-category-listing">
              <TrendingPosts isLoading />
            </div>
          </Col>
        </Row>
      ) : isError ? (
        <div className={"text-center text-danger"}>{errorMessage}</div>
      ) : (
        data.map((postType, index) => postType.totalPosts ? (
          <React.Fragment key={index}>
            <div className="d-flex justify-content-between align-items-center main-heading-ribbon">
              <div className="ribbon-heading-wrap">
                <div className="ribbon-heading">
                  <h2 className="mt-2">Newest arrival in {postType.name}</h2>
                </div>
              </div>
              <a href="javascript:void(0);" className="view-btn">
                <b>View All</b>
              </a>
            </div>
            <Row key={index}>
              <Col lg={9}>
                <LatestPost {...postType} />
              </Col>
              <Col lg={3}>
                <div className="blog-card  trending-category-listing">
                  <TrendingPosts {...postType} />
                </div>
              </Col>
            </Row>
          </React.Fragment>
        ):null)
      )}
      <ContactUs />
    </>
  );
};

export default HomeContainer;
