import React from 'react';
import {
    withScriptjs,
    withGoogleMap,
    GoogleMap,
    Marker
} from "react-google-maps";

const MapComponent = React.memo(() => {
    const MapUrl = 'https://maps.googleapis.com/maps/api/js?key=' + process.env.REACT_APP_GOOGLE_MAP_API_KEY + '&v=3.exp&libraries=geometry,drawing,places';
    const MapWithAMarker = withScriptjs(
        withGoogleMap((props) =>
            <GoogleMap
                defaultZoom={15}
                defaultCenter={{ lat: 22.7196, lng: 75.8577 }}
            >
                <Marker
                    position={{ lat: 22.7196, lng: 75.8577 }}
                />
            </GoogleMap>
        )
    );

    return (
        <MapWithAMarker
            googleMapURL={MapUrl}
            loadingElement={<div style={{ height: `100%` }} />}
            containerElement={<div style={{ height: `400px` }} />}
            mapElement={<div style={{ height: `100%` }} />}
        />
    );
})

export default MapComponent;